@import url(https://fonts.googleapis.com/css2?family=Antic+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Maitree:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#app{width:100vw}#app h3{text-align:center}

.grade-distribution{width:100%;box-sizing:border-box;z-index:1}.grade-distribution .min,.grade-distribution .max,.grade-distribution .distribution{display:inline-block;vertical-align:middle}.grade-distribution .min,.grade-distribution .max{width:15%}.grade-distribution .min span,.grade-distribution .min small,.grade-distribution .max span,.grade-distribution .max small{display:block}.grade-distribution .min small,.grade-distribution .max small{line-height:90%}.grade-distribution .distribution{border:1px solid #EAEAEA;width:calc(70% - 10px);min-height:1rem;background-color:white;margin:0 5px;position:relative}.grade-distribution .distribution .sep{z-index:2;position:absolute;top:0;bottom:0;background-color:rgba(0,100,255,0.4);width:4px}.grade-distribution .distribution .avg{position:absolute;left:0;right:0;top:-3px;bottom:0;vertical-align:middle;text-align:center;z-index:100;color:black;font-weight:bold}.grade-distribution .distribution small{position:absolute;top:-15px;left:50%;transform:translateX(-50%)}.grade-distribution .min{text-align:right}

.tile{box-sizing:border-box;display:inline-block;vertical-align:top}.tile .title{font-family:'Antic Slab', serif;background-color:#eee}.tile .inner{font-family:'Maitree', serif;box-shadow:0 0 5px 5px rgba(240,240,240,0.5);border-radius:5px;margin:15px;padding:10px;border:1px solid #424242;transition:border ease-in-out 200ms;overflow:hidden;min-height:175px;position:relative}.tile .inner .ant-divider-horizontal.ant-divider-with-text-center::before,.tile .inner .ant-divider-horizontal.ant-divider-with-text-center::after{border-top:1px solid #424242}.tile .inner:hover{border:1px solid #0064ff;cursor:pointer}.tile .inner h2{font-size:1rem;padding:5px 0;color:#0064ff}.tile .inner.sufficient .ant-statistic-content-value{color:#52c41b;font-weight:bold}.tile .inner.insufficient .ant-statistic-content-value{color:#ff4e4e;font-weight:bold}.tile .inner .statistic{float:right;margin:10px 0}.tile .inner .title{margin:-10px;padding:0px 10px;box-shadow:0px 4px 3px rgba(50,50,50,0.05);margin-bottom:10px}.tile .inner .peer{margin:-10px;padding:5px 0;text-align:center;font-weight:bold;position:absolute;bottom:5px;left:10px;right:10px}.tile .inner .peer .ant-divider{margin:0;padding:0}

.tileWrapper{border:1px solid black;border-radius:5px}.tileWrapper .tile{display:inline-block}

#componentsView .component{margin:10px;padding:10px;border:1px solid #424242;border-radius:5px;overflow:hidden}#componentsView .component .progress{float:right}#componentsView .component button{margin-top:20px}#componentsView .component p{margin:0}

#tileDetail{padding:20px;box-sizing:border-box;font-family:'Maitree', serif !important}#tileDetail button,#tileDetail h1{display:inline-block;vertical-align:middle}#tileDetail h1{font-family:'Antic Slab', serif !important;padding:0;margin:0 0 0 10px}#tileDetail .graphWrapper{width:60%;margin:0 auto}@media only screen and (max-width: 768px){#tileDetail .graphWrapper{width:100%}}

.csv-reader-input{margin:20px 0}.csv-reader-input input[type=button]{background-color:yellow !important;margin:40px}

